import React from 'react'

// material-ui
import Box from '@material-ui/core/Box'
import Paper from '@material-ui/core/Paper'

// custom component
import SiteHeader from '../components/Headers/SiteHeader'
import GridList from '../components/Lists/GridList'

// articles data query
import useArticles from '../data/useArticles'

/**
 * Articles page
 * @param {Object} props - component properties
 * @param {Object} props.location - representing where the app is from a router perspective
 * @returns {*}
 * @constructor
 */
function Articles({ location }) {
  const articles = useArticles()
  return (
    <SiteHeader
      title="Articles about modern software development"
      description="Collection of articles about modern software development based on JavaScript, React, Node.js, MongoDb,
      etc and personal thoughts in general"
      location={location}
    >
      <Box clone pt={16} pr={2} pb={2} pl={2}>
        <Paper elevation={0}>
          <GridList edges={articles} location={location} />
        </Paper>
      </Box>
    </SiteHeader>
  )
}

export default Articles
